  color: #8e8e8e;
  font-size: 2em;
  font-weight: 600;
  line-height: 28px;
}

.product__mainImage {
  height: 518px;
  margin-right: 10px;
}

#addProduct {
  width: fit-content;
  background-color: #dc037f;
  border-radius: 50%;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 2px 2px 10px #0000004b;
}

#addProduct a {
  color: #fff;
  padding: 5px 20px;
  font-size: 60px;
  text-decoration: none;
}

.productImage {
  display: none;
}

.custom-file-upload {
  color: #009fe3;
  border: 5px dashed #009fe3;
  border-radius: 10px;
  margin-top: 5px;
  padding: 20px;
}

