  padding: 15px;
  box-shadow: 2px 2px 10px #00012c4b;
}

.product__interaction {
  flex-direction: column;
  align-items: center;
}

.main {
  justify-content: center;
}

.product__cart {
  color: #fff;
  width: fit-content;
  background-color: #dc037f;
